import {ol} from '../constants'
class BaseLayers {
  addBaseLayers (params, view) {
    let options = params || []
    let _view = view || {}
    if (_view) {
      /**
       * 
       * @type {ol.proj.Projection}
       */
      this.projection = ol.proj.get((_view['projection'] ? _view.projection : 'EPSG:3857'))
      /**
       * 
       */
      this.fullExtent = _view['fullExtent'] ? _view.fullExtent : [-180, -90, 180, 90]
      /**
       * 
       */
      this.projection.setExtent(this.fullExtent)
      /**
       * 
       * @desc [0,0]
       * 
       */
      this.origin = _view.origin
      /**
       * 
       * @desc 256 512.
       * 256
       */
      this.tileSize = _view.tileSize
      /**
       * 
       * @type Array
       */
      this.resolutions = _view['resolutions']
    }

    if (!options || !Array.isArray(options) || options.length <= 0) {
      return [new ol.layer.Group({
        layers: [new ol.layer.Tile({
          source: new ol.source.OSM()
        })],
        isBaseLayer: true
      })]
    } else {
      return [new ol.layer.Group({
        layers: this._getBaseLayerGroup(params),
        isBaseLayer: true
      })]
    }
  }

  /**
   * 
   * @returns {ol.layer.Group}
   */
  _getBaseLayerGroup (layerConfig) {
    let [layers, labelLayers, _layers, labelLayersConfig] = [[], [], [], []]
    if (layerConfig && Array.isArray(layerConfig) && layerConfig.length > 0) {
      layerConfig.forEach(config => {
        if (config['layerName'] && config['layerUrl'] && config['layerType']) {
          let layer = null
          switch (config['layerType']) {
            case 'TileXYZ':
              layer = this._getXYZLayer(config)
              break
            case 'TitleWMTS':
              layer = this._getWMTSLayer(config)
              break
            case 'OSM':
              layer = this._getOSMLayer(config)
              break
            case 'ImageWMS':
              layer = this._getImageWMSLayer(config)
              break
            case 'TileWMS':
              layer = this._getTileWMSLayer(config)
              break
          }
          if (layer) layers.push(layer)
          if (config['label']) {
            labelLayersConfig.push(config['label'])
          }
        }
      })
    }
    labelLayers = this._getBaseLayerLabel(labelLayersConfig)
    _layers = layers.concat(labelLayers)
    return _layers
  }

  /**
   * 
   * @param labelLayersConfig
   * @returns {null}
   * @private
   */
  _getBaseLayerLabel (labelLayersConfig) {
    let [labelLayers, _labelLayersLayerNames] = [[], (new Set())]
    if (labelLayersConfig && Array.isArray(labelLayersConfig) && labelLayersConfig.length > 0) {
      labelLayersConfig.forEach(config => {
        if (config['layerName'] && config['layerUrl'] && config['layerType']) {
          _labelLayersLayerNames.add(config['layerName'])
        }
      });
      [...(_labelLayersLayerNames)].forEach(layerName => {
        labelLayersConfig.every(configM => {
          if (configM && configM['layerName'] === layerName) {
            let labelLayer = null
            switch (configM['layerType']) {
              case 'TileXYZ':
                labelLayer = this._getXYZLayer(configM)
                break
              case 'TitleWMTS':
                labelLayer = this._getWMTSLayer(configM)
                break
              case 'ImageWMS':
                labelLayer = this._getImageWMSLayer(configM)
                break
              case 'TileWMS':
                labelLayer = this._getTileWMSLayer(configM)
                break
            }
            if (labelLayer) labelLayers.push(labelLayer)
            return false
          }
          return true
        })
      })
    }
    return labelLayers
  }

  /**
   * XYZ
   * @param config
   * @returns {ol.layer.Tile}
   * @private
   */
  _getXYZLayer (config) {
    let tileUrl = config['layerUrl']
    let tileGrid = new ol.tilegrid.TileGrid({
      tileSize: this.tileSize,
      origin: this.origin,
      extent: this.fullExtent,
      resolutions: this.resolutions
    })
    let tileArcGISXYZ = new ol.source.XYZ({
      wrapX: false,
      tileGrid: tileGrid,
      attributions: (this._getAttribution(config['attribution'])),
      tileSize: this.tileSize,
      opaque: (config['opaque'] === true) ? config['opaque'] : false, // 
      tilePixelRatio: 1, // todo 23
      projection: this.projection,
      // crossOrigin: 'Anonymous',
      tileUrlFunction: function (tileCoord) {
        let url = (tileUrl + '/tile/{z}/{y}/{x}').replace('{z}',
          (tileCoord[0]).toString()).replace('{x}',
          tileCoord[1].toString()).replace('{y}',
          (-tileCoord[2] - 1).toString())
        return url
      }
    })
    let baseLayer = new ol.layer.Tile({
      isBaseLayer: true,
      alias: config['alias'] ? config['alias'] : '',
      isDefault: (config['isDefault'] === true) ? config['isDefault'] : false,
      visible: (config['isDefault'] === true) ? config['isDefault'] : false,
      layerName: config['layerName'] ? config.layerName : '',
      source: tileArcGISXYZ
    })
    return baseLayer
  }

  /**
   * OSM
   * @param config
   * @returns {ol.layer.Tile}
   * @private
   */
  _getOSMLayer (config) {
    let baseLayer = new ol.layer.Tile({
      isBaseLayer: true,
      alias: config['alias'] ? config['alias'] : '',
      isDefault: (config['isDefault'] === true) ? config['isDefault'] : false,
      visible: (config['isDefault'] === true) ? config['isDefault'] : false,
      layerName: config['layerName'] ? config.layerName : '',
      source: new ol.source.OSM({
        attributions: (this._getAttribution(config['attribution'])),
        wrapX: false,
        opaque: (config['opaque'] === true) ? config['opaque'] : false, // 
        url: config['layerUrl'] ? config['layerUrl'] : 'https://{a-c}.tile.openstreetmap.org/{z}/{x}/{y}.png',
        crossOrigin: 'Anonymous'
      })
    })
    return baseLayer
  }

  /**
   * WMTS
   * @param config
   * @returns {ol.layer.Tile}
   * @private
   */
  _getWMTSLayer (config) {
    let projection = ol.proj.get('EPSG:4326')
    let size = ol.extent.getWidth(projection.getExtent()) / 256
    let resolutions = new Array(19)
    let matrixIds = new Array(19)
    for (let z = 0; z < 19; ++z) {
      resolutions[z] = size / Math.pow(2, z)
      matrixIds[z] = z
    }
    let layer = new ol.layer.Tile({
      isBaseLayer: true,
      alias: config['alias'] ? config['alias'] : '',
      isDefault: (config['isDefault'] === true) ? config['isDefault'] : false,
      layerName: config['layerName'] ? config.layerName : '',
      visible: (config['isDefault'] === true) ? config['isDefault'] : false,
      source: new ol.source.WMTS({
        url: config['layerUrl'],
        layer: config['layer'],
        matrixSet: 'c',
        format: 'tiles',
        crossOrigin: 'Anonymous',
        attributions: (this._getAttribution(config['attribution'])),
        projection: projection,
        tileGrid: new ol.tilegrid.WMTS({
          origin: ol.extent.getTopLeft(projection.getExtent()),
          resolutions: resolutions,
          matrixIds: matrixIds
        }),
        style: 'default',
        wrapX: false
      })
    })
    return layer
  }

  /**
   * Images WMS 
   * @param config
   * @private
   */
  _getImageWMSLayer (config) {
    let proj = this.projection.getCode()
    let layer = new ol.layer.Image({
      isBaseLayer: true,
      alias: config['alias'] ? config['alias'] : '',
      isDefault: (config['isDefault'] === true) ? config['isDefault'] : false,
      layerName: config['layerName'] ? config.layerName : '',
      visible: (config['isDefault'] === true) ? config['isDefault'] : false,
      opacity: (config['opacity'] && (typeof config['opacity'] === 'number')) ? config['opacity'] : 1,
      source: new ol.source.ImageWMS({
        url: config['layerUrl'],
        attributions: (this._getAttribution(config['attribution'])),
        params: {
          LAYERS: config['layers'], // require
          STYLES: config['style'] ? config['style'] : '',
          VERSION: config['version'] ? config['version'] : '1.1.1',
          WIDTH: config['width'] ? config['width'] : 256,
          HEIGHT: config['height'] ? config['height'] : 256,
          BBOX: config['bbox'], // require
          SRS: !proj ? 'EPSG:4326' : proj,
          CRS: !proj ? 'EPSG:4326' : proj,
          REQUEST: 'GetMap',
          TRANSPARENT: true,
          TILED: (config['tiled'] === false) ? config['tiled'] : true,
          TILESORIGIN: config['tiledsorrigin'] ? config['tiledsorrigin'] : undefined,
          SERVICE: 'WMS',
          FORMAT: config['format'] ? config['format'] : 'image/png'
        },
        wrapX: false
      })
    })
    return layer
  }

  /**
   * Title WMS 
   * @param config
   * @returns {ol.layer.Tile}
   * @private
   */
  _getTileWMSLayer (config) {
    let proj = this.projection.getCode()
    let layer = new ol.layer.Tile({
      isBaseLayer: true,
      alias: config['alias'] ? config['alias'] : '',
      isDefault: (config['isDefault'] === true) ? config['isDefault'] : false,
      layerName: config['layerName'] ? config.layerName : '',
      visible: (config['isDefault'] === true) ? config['isDefault'] : false,
      opacity: (config['opacity'] && (typeof config['opacity'] === 'number')) ? config['opacity'] : 1,
      source: new ol.source.TileWMS({
        url: config['layerUrl'],
        attributions: (this._getAttribution(config['attribution'])),
        params: {
          LAYERS: config['layers'], // require
          STYLES: config['style'] ? config['style'] : '',
          VERSION: config['version'] ? config['version'] : '1.1.1',
          WIDTH: config['width'] ? config['width'] : 256,
          HEIGHT: config['height'] ? config['height'] : 256,
          BBOX: config['bbox'], // require
          SRS: !proj ? 'EPSG:4326' : proj,
          CRS: !proj ? 'EPSG:4326' : proj,
          REQUEST: 'GetMap',
          TRANSPARENT: true,
          TILED: (config['tiled'] === false) ? config['tiled'] : true,
          TILESORIGIN: config['tiledsorrigin'] ? config['tiledsorrigin'] : undefined,
          SERVICE: 'WMS',
          FORMAT: config['format'] ? config['format'] : 'image/png'
        },
        wrapX: false
      })
    })
    return layer
  }

  /**
   * 
   * @returns {Xr.Attribution|Jr.Attribution|Wr.Attribution|*|ol.Attribution|ol.control.Attribution}
   * @private
   */
  _getAttribution (params) {
    if (!params) {
      params = {}
      params['url'] = 'https://aurorafe.github.io'
      params['messages'] = 'contributors.'
      params['title'] = 'HMap'
    }
    let attribution = new ol.Attribution({
      html: '&copy; ' + '<a href="' + params['url'] + '">' + params['title'] + '</a> ' + params['messages']
    })
    return attribution
  }
}

export default BaseLayers



