import mix from '../utils/mixin'
import { ol, proj4 } from '../constants'

import BaseLayers from './baseLayer'
import Controls from './Controls'
import Interactions from './Interactions'
import View from './View'

import Layer from '../layer/Layer'
import Feature from '../feature/feature'
import Style from '../style/Style'
import Overlay from '../overlay/Overlay'

class Map extends mix(BaseLayers, Controls, Interactions, View, Style, Layer, Feature, Overlay) {
  constructor () {
    super()
    this.addPointHandlerClick = null
    this.plotDraw = null // 
    this.plotEdit = null
    this._lastDrawInteractionGeometry = null
    window.ObservableObj = new ol.Object()
    proj4.defs('EPSG:4490', '+proj=longlat +ellps=GRS80 +no_defs')
    ol.proj.setProj4(proj4)

    /**
     * 
     * @type {Array}
     */
    this.currentMapLines = []
    /**
     * 
     * @type {Array}
     */
    this.currentMapPoints = []
    /**
     * 
     * @type {Array}
     */
    this.currentMapPolygon = []
    /**
     * 
     * @type {Array}
     */
    this.lineLayers = new Set()
    /**
     * 
     * @type {Array}
     */
    this.pointLayers = new Set()
    /**
     * 
     * @type {Array}
     */
    this.polygonLayers = new Set()
    /**
     * 
     * @type {null}
     */
    this.circleSerachFeat = null
    /**
     * 
     * @type {null}
     */
    this.popupOverlay = null
  }

  /**
   * 
   * @param mapDiv
   * @param params
   */
  initMap (mapDiv, params) {
    let options = params || {}
    /**
     * 
     * @type {ol.Map}
     */
    this.map = new ol.Map({
      target: mapDiv,
      loadTilesWhileAnimating: false,
      loadTilesWhileInteracting: false,
      logo: this._addCopyRight((options['logo'] || {})),
      layers: this.addBaseLayers(options['baseLayers'], options['view']),
      view: this._addView(options['view']),
      interactions: this._addInteractions(options['interactions']),
      controls: this._addControls(options['controls'])
    })

    this.map.on('click', event => {
      console.log(event.coordinate)
    })
  }

  /**
   * 
   * @returns {boolean}
   * @private
   */
  _addCopyRight (params) {
    let logo = {
      href: 'https://aurorafe.github.io',
      src: 'data:image/png;base64,'
    }
    if (params['href']) {
      logo.href = params['href']
    }
    if (params['src']) {
      logo.src = params['src']
    }
    return logo
  }

  /**
   * 
   * @returns {ol.Map}
   */
  getMap () {
    return this.map
  }

  /**
   * 
   * @param map
   */
  setMap (map) {
    if (map && map instanceof ol.Map) {
      this.map = map
    }
  }

  /**
   * 
   */
  updateSize () {
    this.map.updateSize()
  }
}

export default Map



